/**
 * WordPress dependencies
 */
import { __ } from "@wordpress/i18n";
import { useSelect } from "@wordpress/data";
import {
  useBlockProps,
  useInnerBlocksProps,
  useSetting,
  __experimentalRecursionProvider as RecursionProvider,
  __experimentalUseHasRecursion as useHasRecursion,
  store as blockEditorStore,
  Warning,
} from "@wordpress/block-editor";
import { useEntityProp, useEntityBlockEditor } from "@wordpress/core-data";

/**
 * Internal dependencies
 */
import { useCanEditEntity } from "../icons";

function ReadOnlyContent({ userCanEdit, postType, postId }) {
  const [, , content] = useEntityProp("postType", postType, "content", postId);
  const blockProps = useBlockProps();
  return content?.protected && !userCanEdit ? (
    <div {...blockProps}>
      <Warning>{__("This content is password protected.")}</Warning>
    </div>
  ) : (
    <div
      {...blockProps}
      dangerouslySetInnerHTML={{ __html: content?.rendered }}></div>
  );
}

function EditableContent({ layout, context = {} }) {
  const { postType, postId } = context;
  const themeSupportsLayout = useSelect((select) => {
    const { getSettings } = select(blockEditorStore);
    return getSettings()?.supportsLayout;
  }, []);
  const defaultLayout = useSetting("layout") || {};
  const usedLayout = !layout?.type
    ? { ...defaultLayout, ...layout, type: "default" }
    : { ...defaultLayout, ...layout };
  const [blocks, onInput, onChange] = useEntityBlockEditor(
    "postType",
    postType,
    { id: postId }
  );

  const props = useInnerBlocksProps(
    useBlockProps({ className: "entry-content" }),
    {
      value: blocks,
      onInput,
      onChange,
      __experimentalLayout: themeSupportsLayout ? usedLayout : undefined,
    }
  );
  return <div {...props} />;
}

function Content(props) {
  const { context: { queryId, postType, postId } = {} } = props;
  const isDescendentOfQueryLoop = Number.isFinite(queryId);
  const userCanEdit = useCanEditEntity("postType", postType, postId);
  const isEditable = userCanEdit && !isDescendentOfQueryLoop;

  return isEditable ? (
    <EditableContent {...props} />
  ) : (
    <ReadOnlyContent
      userCanEdit={userCanEdit}
      postType={postType}
      postId={postId}
    />
  );
}

function Placeholder({ layoutClassNames }) {
  const blockProps = useBlockProps({ className: layoutClassNames });
  return (
    <div {...blockProps}>
      <p>
        {__(
          "This is the Post Content block, it will display all the blocks in any single post or page.",
          "smt-theme"
        )}
      </p>
      <p>
        {__(
          "That might be a simple arrangement like consecutive paragraphs in a blog post, or a more elaborate composition that includes image galleries, videos, tables, columns, and any other block types.",
          "smt-theme"
        )}
      </p>
      <p>
        {__(
          "If there are any Custom Post Types registered at your site, the Post Content block can display the contents of those entries as well.",
          "smt-theme"
        )}
      </p>
    </div>
  );
}

function RecursionError() {
  const blockProps = useBlockProps();
  return (
    <div {...blockProps}>
      <Warning>{__("Block cannot be rendered inside itself.")}</Warning>
    </div>
  );
}

export default function PostContentEdit({
  context,
  attributes,
  __unstableLayoutClassNames: layoutClassNames,
}) {
  const { postId: contextPostId, postType: contextPostType } = context;
  const { layout = {} } = attributes;
  const hasAlreadyRendered = useHasRecursion(contextPostId);

  if (contextPostId && contextPostType && hasAlreadyRendered) {
    return <RecursionError />;
  }

  return (
    <RecursionProvider uniqueId={contextPostId}>
      {contextPostId && contextPostType ? (
        <Content context={context} layout={layout} />
      ) : (
        <Placeholder layoutClassNames={layoutClassNames} />
      )}
    </RecursionProvider>
  );
}
